.icon-destroy {
  // border: 2px solid rgb(237, 79, 79);
  border-radius: 3px;
}

.description {
  word-break: break-all;
  display: block;
  line-height: 1.2;
  transition: color 0.4s;
  font-weight: 400;
  // color: #4d4d4d;
}
.todo-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.todo-list li {
  position: relative;
  font-size: 24px;
  border-bottom: 1px solid #ededed;
}

.todo-list li:last-child {
  border-bottom: none;
}

.todo-list li.editing {
  border-bottom: none;
  // padding: 0;
}

.todo-list li.editing .edit {
  display: block;
  width: calc(100% - 43px);
  padding: 12px 16px;
  margin: 0 0 0 43px;
}

.todo-list li .toggle {
  text-align: center;
  width: 40px;
  /* auto, since non-WebKit browsers doesn't support input styling */
  height: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  border: none; /* Mobile Safari */
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
}

.todo-list li .toggle {
  opacity: 0;
}

.todo-list li .toggle + label {
  /*
		Firefox requires `#` to be escaped - https://bugzilla.mozilla.org/show_bug.cgi?id=922433
		IE and Edge requires *everything* to be escaped to render, so we do that instead of just the `#` - https://developer.microsoft.com/en-us/microsoft-edge/platform/issues/7157459/
	*/
  background-image: url('data:image/svg+xml;utf8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2240%22%20height%3D%2240%22%20viewBox%3D%22-10%20-18%20100%20135%22%3E%3Ccircle%20cx%3D%2250%22%20cy%3D%2250%22%20r%3D%2250%22%20fill%3D%22none%22%20stroke%3D%22%23ededed%22%20stroke-width%3D%223%22/%3E%3C/svg%3E');
  background-repeat: no-repeat;
  background-position: center left;
}

.todo-list li .toggle:checked + label {
  background-image: url('data:image/svg+xml;utf8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2240%22%20height%3D%2240%22%20viewBox%3D%22-10%20-18%20100%20135%22%3E%3Ccircle%20cx%3D%2250%22%20cy%3D%2250%22%20r%3D%2250%22%20fill%3D%22none%22%20stroke%3D%22%23bddad5%22%20stroke-width%3D%223%22/%3E%3Cpath%20fill%3D%22%235dc2af%22%20d%3D%22M72%2025L42%2071%2027%2056l-4%204%2020%2020%2034-52z%22/%3E%3C/svg%3E');
}

.todo-list li label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 85px 15px 60px;
}

.todo-list li label .created {
  font-size: 13px;
  color: gray;
}

.todo-list li.completed label .description {
  color: #cdcdcd;
  text-decoration: line-through;
}

.todo-list li .icon {
  position: absolute;
  top: 0;
  bottom: -3px;
  width: 30px;
  height: 40px;
  font-size: 34px;
  color: #cc9a9a;
  // margin: auto 0 11px;
  align-self: center;
  transition: color 0.2s ease-out;
  cursor: pointer;
}

.todo-list li .icon:hover {
  color: #af5b5e;
}

.todo-list li .icon-destroy {
  right: 10px;
}

.todo-list li .icon-destroy:hover {
  border: #af5b5e 1px solid;
  border-radius: 5px;
}

.todo-list li .icon-destroy:after {
  content: '×';
  color: red;
  margin-bottom: 15px;
}

.todo-list li .icon-edit {
  font-size: 19px;
  right: 45px;
}

.todo-list li .icon-edit:hover {
  border: green 1px solid;
  border-radius: 5px;
}

.todo-list li .icon-edit:after {
  content: '\0021';
  color: green;
}
// '✎'
.todo-list li .edit {
  display: none;
}

.todo-list li.editing:last-child {
  margin-bottom: -1px;
}

//  .list-group-item.active
.Active {
  background-color: white;
  font-size: 1.8rem;
  color: rgb(10, 10, 227);
}

.todo-list li label.created {
  justify-self: end;
}

.completed {
  color: grey;
  text-decoration: line-through;
}

.completedActive {
  color: grey;
  text-decoration: line-through;
  font-size: 1.8rem;
}

.editActive {
  background-color: rgba(165, 255, 170, 0.596);
  border-radius: 5px;
}

.editActiveComplet {
  background-color: gray;
  border-radius: 5px;
}
